"use strict";
var d3 = require("d3-selection");
var ctrl_1 = require("./ctrl");
var Led = (function () {
    function Led(div) {
        console.log("LED");
        div = div
            .append("center")
            .attr("margin-top", "20px");
        div.append("div")
            .attr("class", "title")
            .html("LED Settings");
        this.color = div
            .append("div")
            .style("width", "80px")
            .style("height", "80px")
            .style("background-color", "white")
            .style("border", "1px solid #eee");
        this.time = div
            .append("pre")
            .style("font-size", "80%")
            .html("-");
        this.rgb = div
            .append("div")
            .selectAll("div")
            .data(["R", "G", "B"])
            .enter()
            .append("div");
        this.rgb
            .each(function (n, x, y) {
            var o = d3.select(y[x]);
            o.append("span")
                .style("font-size", "180%")
                .style("color", "#888")
                .html(n + " ");
            o.append("input")
                .attr("type", "range")
                .attr("min", 0)
                .attr("max", 255)
                .style("width", "80%")
                .style("margin", "30px 0")
                .on("input", function () {
                var v = +this.value;
                ctrl_1.ctrl.request("led:" + n.toLowerCase() + ":" + v);
            });
        });
    }
    Led.prototype.start = function () {
        ctrl_1.ctrl.request("led:");
    };
    Led.prototype.stop = function () {
    };
    Led.prototype.update = function (val) {
        this.color.style("background-color", "#" + val);
        var color = parseInt(val, 16);
        this.rgb
            .each(function (n, x, y) {
            var o = d3.select(y[x]);
            var v = (color >> (16 - x * 8)) & 0xff;
            o.select("input").property("value", v);
        });
        this.time.html("0x" + val);
    };
    return Led;
}());
exports.Led = Led;



