"use strict";
var d3 = require("d3-selection");
// var $ = require("jquery");
var ctrl_1 = require("./ctrl");
var led_1 = require("./led");
var diode_1 = require("./diode");
var Options = (function () {
    function Options() {
    }
    return Options;
}());
var UI = (function () {
    function UI() {
        this.console = "";
        this.currentTab = 1;
    }
    UI.prototype.show = function (name, opt) {
    };
    UI.prototype.init = function (root) {
        var _this = this;
        var tabs = [0, 1, 2];
        var names = ["ConsoleX", "Diode", "Lights"];
        this.headers = d3.select(root)
            .append("div")
            .attr("class", "tabs")
            .selectAll("div")
            .data(tabs)
            .enter()
            .append("div")
            .attr("class", function (n) { return n == 0 ? "active" : ""; })
            .attr("id", function (n) { return "h" + n; })
            .html(function (n) { return names[n]; })
            .on("click", function (n) {
            _this.goto(n);
        });
        this.contents = d3.select(root)
            .append("div")
            .attr("class", "contents")
            .selectAll("div")
            .data(tabs)
            .enter()
            .append("div")
            .attr("class", function (n) { return "content " + (n == 0 ? "active" : ""); })
            .attr("id", function (n) { return "c" + n; });
        var mainContent = d3.select("#c0");
        this.connectBtn = mainContent
            .append("div")
            .attr("class", "btn")
            .html("Connect")
            .on("click", function () {
            ctrl_1.ctrl.connect();
        });
        this.consoleDiv = mainContent
            .append("div")
            .style("display", "block")
            .html("---");
        this.diode = new diode_1.Diode(d3.select("#c1"));
        this.led = new led_1.Led(d3.select("#c2"));
        var element = document.createElement('style');
        var css = require("./style.css");
        element.type = "text/css";
        element.innerHTML = css;
        document.body.appendChild(element);
        this.log("Init");
        setTimeout(function () {
            ctrl_1.ctrl.connect();
        }, 250);
    };
    UI.prototype.connectState = function (s) {
        this.connectBtn.html(s);
        this.connectBtn.style("display", (s == "Disconnect") ? "none" : "block");
        if (s == "Disconnect")
            this.goto(2);
    };
    UI.prototype.log = function (s) {
        var t = new Date().toTimeString();
        this.console += "<small>[" + t + "]</small> " + s + "<br>";
        this.consoleDiv.html(this.console);
    };
    UI.prototype.goto = function (id) {
        this.headers.attr("class", "");
        this.contents.attr("class", "content");
        d3.select("#h" + id).attr("class", "active");
        d3.select("#c" + id).attr("class", "active");
        this.currentTab = id;
        console.log("goto", id);
        if (id == 1) {
            this.diode.start();
        }
        else {
            this.diode.stop();
        }
        if (id == 2) {
            this.led.start();
        }
    };
    UI.prototype.next = function () {
        this.currentTab++;
        if (this.currentTab >= 3)
            this.currentTab = 0;
        this.goto(this.currentTab);
    };
    return UI;
}());
exports.UI = UI;
exports.ui = new UI();



